      localStorage.getItem("idUsuarioLogueadoTramite") == null ||
      localStorage.getItem("idUsuarioLogueadoTramite") == 0
    ) {
      // console.log('entró a false');
      localStorage.setItem("areaTramite", this.$route.params.idArea);
      localStorage.setItem("tipoTramite", this.$route.params.idTipoTramite);
      localStorage.setItem("idTramite", this.$route.params.idTramite);
      GlobalEventBus.$emit('logout')
      this.$router.push('/login')
    } else {
      // console.log('entró a true');
      if (this.$route.params.paso != null) {
        let paso = this.$route.params.paso;

