          this.flagBotonPago = false;
        } else if (paso == 4) {
          //Se pago correctamente
          this.flagBotonPago = false;
        } else {
          this.flagBotonPago = true;
        }
      }
      this.cargarTipoTramite(); 
      this.inicializar();
      if (this.$route.params.idTramite == 0) {
        this.cargarRequisitoTramite();
      } else {
