    validarExpediente(){
      var urlValidacion =Constantes.rutaParametros+"documentoExpediente/7586/"+this.numeroDocumentoAnexo+"/"+this.anioAnexo +"/0" 
      console.log(urlValidacion);
      axios
        .get(urlValidacion)
        .then(response => {
          var expedienteJson = response.data;
          console.log("tamaño JSON:" + expedienteJson.data.length);
          this.mensajeValidacionDocumentoGlosa =
            "El documento no fue encontrado";
          //this.$swal('El documento no fue encontrado');
          if (expedienteJson.data.length >0) {
            let minExpediente=expedienteJson.data[0].expediente.idExpediente;
            for (let index = 0; index < expedienteJson.data.length; index++) {
              console.log("idExpediente")
              console.log(expedienteJson.data[index].expediente.idExpediente)
              console.log("idExpedienteMenor: "+ minExpediente)
              
              if(minExpediente>expedienteJson.data[index].expediente.idExpediente){
                minExpediente=expedienteJson.data[index].expediente.idExpediente;
              }
              
            }
            this.permiteEditar=true;
            //this.idExpedienteAnexo =expedienteJson.data[0].expediente.idExpediente;
            this.idExpedienteAnexo =minExpediente;
            this.expedienteValidado=true;
            //this.$swal('El documento fue validado con éxito');
          } else {
            this.expedienteValidado=false;
          }
          console.log(expedienteJson);
        })
        .catch(e => console.log(e));
    },
    validarExpedienteAnexo(){
      var urlValidacion =Constantes.rutaParametros+"documentoExpediente/7586/"+this.numeroDocumentoAnexo+"/"+this.anioAnexo +"/813" 
      console.log(urlValidacion);
      axios
        .get(urlValidacion)
        .then(response => {
          var expedienteJson = response.data;
          console.log("tamaño JSON:" + expedienteJson.data.length);
          this.mensajeValidacionDocumentoGlosa =
