            this.permiteEditar=true;
            this.idExpedienteAnexo =expedienteJson.data[0].expediente.idExpediente;
            this.expedienteValidado=true;
            //this.$swal('El documento fue validado con éxito');
          } else {
            this.validarExpedienteAnexoPlataforma();
          }
          console.log(expedienteJson);
        })
        .catch(e => console.log(e));
    },
