          this.idSubConcepto = response.data.data.idSubConcepto;
          this.cargarPrecio();
          if(response.data.data.id008EquivalenciaOracle==38){
            this.isSolicitud=true;
            if(this.$route.params.idTramite==0){
              this.permiteEditar=false;
            }
