      this.respuestaPago = Constantes.respuestaPagoTramite + paramIdArea;
      this.respuestaPago += "&idTipoTramite=" + paramIdTipoTramite +"&idTramite=" + this.idTramite  + "&base=" +this.precioTramite;
      //this.respuestaPago=Constantes.rutaPagos+'respuestaVisa/'+'PAGO-TRAMITE-DOCUMENTARIO/'+this.idTramite*1111
      //this.respuestaPago="http://localhost/plataforma/#/tramite/ce/759&9654&"+this.idTramite+"&0"
      //this.respuestaPago="http://localhost:8080/#/tramite/ce/759&9654&"+this.idTramite+"&0"
      

      //var rutadev='http://localhost:8929/';
      var rutaNumeroPedido= Constantes.rutaPagos+"pedido/"+this.idTramite;
      axios
          .get(rutaNumeroPedido)
          .then(response => {
            console.log(response)
            if(response.data.success){
              this.respuestaPago+="&numeroPedido="+response.data.data.purchaseNumber
              var Pago = {};
              Pago.nroOrdenPago = response.data.data.purchaseNumber;
              Pago.monto = this.precioTramite;
              Pago.modulo = "PAGO-TRAMITE-DOCUMENTARIO";
              var rutaprod = Constantes.rutaPagos + "inicio-pago";
              axios
                .post(rutaprod, Pago)
                .then(response => {
                  console.log("Respuesta pago")
                  console.log(response)
                  console.log(response.success)
                  if(response.data.success){
                    this.mostrarVisa=true;
                    this.mostrarContinuar=false;
                    EventBus.$emit("loadershow", false);
                    // console.log(response.data);
                    let recaptchaScript = document.createElement("script");
                    recaptchaScript.setAttribute("src", response.data.params.dataSrc);
                    recaptchaScript.setAttribute("data-sessiontoken",response.data.params.dataSessiontoken);
                    recaptchaScript.setAttribute("data-channel",response.data.params.dataChannel);
                    recaptchaScript.setAttribute("data-merchantid",response.data.params.dataMerchantid);
                    recaptchaScript.setAttribute("data-merchantlogo",response.data.params.dataMerchantlogo);
                    recaptchaScript.setAttribute("data-purchasenumber",response.data.params.dataPurchasenumber);
                    recaptchaScript.setAttribute("data-amount",response.data.params.dataAmount);
                    recaptchaScript.setAttribute("data-expirationminutes",response.data.params.dataExpirationminutes);
                    recaptchaScript.setAttribute("data-timeouturl",response.data.params.dataTimeouturl);
                    let form = document.getElementById("formPago");
                    form.appendChild(recaptchaScript);
                  }else{
                    this.$swal({
                      icon: "info",
                      title: "Aviso",
                      text:
                        "Tenemos problemas de comunicación con la plataforma de pagos NIUBIZ. Favor vuelva a intentar en unos minutos."
                    });
                    EventBus.$emit("loadershow", false);
                    this.mostrarContinuar=false;
                  }
                  
                })
                .catch(e => console.log(e));
            }else{
              console.log('entro a false numero pedido')
            }
          })
          .catch(e => console.log(e));
      
    },
