          }
          console.log('pintando primera carga y estado inicial bd:' + this.primeraCarga + ' ' + tramiteResponse.id011Estado.idParametro);
          if (this.primeraCarga == 0) {
            this.estadoInicial = tramiteResponse.id011Estado.idParametro;
            this.primeraCarga = 1;
            if (this.estadoInicial == 22)
              this.mensajeFinal = "La Solicitud se encuentra Creada";
            if (this.estadoInicial == 23)
              this.MensajeFinal();
              //this.mensajeFinal = "La Solicitud se encuentra En Revisión";
            if (this.estadoInicial == 24)
              this.mensajeFinal = "La Solicitud se encuentra En Trámite";
            if (this.estadoInicial == 27)
