              tramiteResponse.listaRequisito[i].ayuda == null ||
              tramiteResponse.listaRequisito[i].ayuda == ""
            ) {
              RequisitoModelo.ayuda = tramiteResponse.listaRequisito[i].ayuda;
            } else {
              RequisitoModelo.ayuda = tramiteResponse.listaRequisito[i].ayuda;
            }
            RequisitoModelo.linkFormato =
              tramiteResponse.listaRequisito[i].requisito.linkFormato;
            //console.log('pintando idRequisito::::' + tramiteResponse.listaRequisito[i].idRequisitoTramite);
            //console.log('pintando linkformato::::' + tramiteResponse.listaRequisito[i].linkFormato);
            RequisitoModelo.urlDescarga =
              tramiteResponse.listaRequisito[i].idTramiteRequisito > 0
                ?  Constantes.filesRecupera+ "4/" +
                //? "https://mz-services.miraflores.gob.pe:8923/recuperarEntidadArchivo/4/" +
                  tramiteResponse.listaRequisito[i].idTramiteRequisito +
                  "/0"
                : null;
            RequisitoModelo.urlDescargaFormato = Constantes.filesRecupera+ "5/"+
              //"https://mz-services.miraflores.gob.pe:8923/recuperarEntidadArchivo/5/" +
              tramiteResponse.listaRequisito[i].requisito.idRequisito +
              "/0";
