              tramiteResponse.token != null &&
              tramiteResponse.token != "" &&(this.idpagosamd==null || this.idpagosamd==0)
            ) {
              console.log('entró a querer finalizar pago:' + tramiteResponse.token);
              this.FinalizarPago(tramiteResponse.token,tramiteResponse.numeroPedido);
            }
          }
        })
        .catch(e => console.log(e));
    },
    FinalizarPago(token,numeroPedido) {
      
      console.log("pintando token:" + token);
      var dataPost = new FormData();
      var AuthorizationRequest = {};
      AuthorizationRequest.transactionToken = token;
      AuthorizationRequest.idEntidad=this.idTramite;
      dataPost.append(
        "AuthorizationRequestString",
        JSON.stringify(AuthorizationRequest)
      );
      this.$swal({
          title: "Procesando",
          allowOutsideClick: false,
          onBeforeOpen: () => {
            this.$swal.showLoading();
          }
      });
      axios
        .post(
          Constantes.rutaPagos +
            "respuestaVisa/PAGO-TRAMITE-DOCUMENTARIO/" +
            numeroPedido,
          dataPost
        )
        .then(response => {
          if (response.data.success) {
            this.mostrarVisa=true;
             var url1 =
            Constantes.rutaTramite + "tramites/" + this.idTramite + "/0/0/-";
            axios.get(url1)
            .then(response => {
              this.permiteEditar=false;
              var tramiteResponse = response.data.data[0];
              this.idpagosamd=tramiteResponse.idPagoSamd;
              console.log('pintando pago samd de tramite' + this.idpagosamd);
              this.$swal.close();
              this.CargarTabInicio();  
            }) 
            
          } else {
            this.$swal.close();
            this.$swal({
              icon: "info",
              title: "Aviso",
              text:
                "Sucedió un error. Favor vuelva a intentar en unos minutos. " +
                response.data.message
            });
          }
        })
        .catch(e => console.log(e));
    },
