    cargarUnidadOrganica() {
      axios
        //.get(Constantes.rutaTramite + "unidadorganica/" + this.$route.params.idArea)
        .get(Constantes.rutaTramite + "area/" + this.$route.params.idArea+"/0")
        .then(response => {
          //this.nombreArea = response.data.data[0].uniorgnom; 
          console.log('pintando areas')   
          console.log(response.data)
          this.nombreArea = response.data[0].nombreArea;
          this.nombreArea = this.nombreArea.toLowerCase();
          var nombre = this.nombreArea.split(' ');
          var i = 0;
          var nombreConcatenado = '';
          while(i < nombre.length){ 
            if(nombre[i].length>2) nombre[i] = nombre[i].charAt(0).toUpperCase() + nombre[i].slice(1);
            
            if(nombreConcatenado=='') {
              nombreConcatenado = nombre[i]; 
            }else{
              nombreConcatenado = nombreConcatenado + ' ' +nombre[i];
            }
            i++;
          }
            this.nombreArea = nombreConcatenado;
        })
        .catch(e => console.log(e));
    },
