    cambiarPaso() {
      // alert(1)
      var nuevoPaso = 0;
      var indice = 0;
      var orden = 0;
      var pasoActual = this.pasoActual;
      // console.log('cambiar paso 1031:' + this.pasoActual);
      while (indice < this.listaPasos.length && nuevoPaso == 0) {
        if (this.listaPasos[indice].orden > this.ordenActual) {
          nuevoPaso = this.listaPasos[indice].idPaso.idPasos;
          orden = this.listaPasos[indice].orden;
        }
        indice++;
      }
      // console.log('cambiar paso 1039:' + pasoActual);
      if (pasoActual == 2) {
        if (this.permiteEditar && this.$route.params.paso == 0) {
          // console.log('1260');
          this.ActualizarTramite(nuevoPaso, orden);
        } else {
          // console.log('1262');
          if(this.idTramite>0){
            this.CambioSimple(nuevoPaso, orden, -1);
          }
          
        }
      }
      if (pasoActual == 5 && this.permiteEditar) {
        this.$swal({
          title:
            this.estadoInicial == 27
              ? "¿Seguro de Subsanar Solicitud?"
              : "¿Seguro de Crear Solicitud?",
          text: this.estadoInicial == 27? "Antes de continuar revise sus adjuntos,  luego de esto ya no podra realizar cambios": "Antes de continuar revise sus adjuntos,"+ (this.precioTramite>0?"se procederá con el pago":"")+" luego de esto ya no podra realizar cambios",
          icon: "info",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          cancelButtonText: "No",
          confirmButtonText: "Sí"
        }).then(result => {
          if (result.value) {
            this.MensajeFinal();
            this.permiteEditar = false;

            // console.log("NUEVO PASO:"+nuevoPaso);
            
            // console.log('----------')
            // console.log(this.precioTramite)
            // nuevoPaso = this.precioTramite <= 0 ? 4 : nuevoPaso
            // orden = this.precioTramite <= 0 ? 4 : orden
            this.CambioSimple(nuevoPaso, orden, 0);
          }
        });
      } else if (pasoActual == 5 && !this.permiteEditar) {
        // console.log("NUEVO PASO:"+nuevoPaso);
        this.CambioSimple(nuevoPaso, orden, -1);
      }
      if (pasoActual == -1) {
        // console.log('cambiar paso:' + nuevoPaso + ', ' + orden);
        this.CambioSimple(nuevoPaso, orden, -1);
      }
      if (pasoActual == 3) {
        this.CambioSimple(nuevoPaso, orden, -1);
      }
      if(pasoActual==4){
        this.$router.push('/')
      }
      return nuevoPaso;
    },
