    onFileRequisitoSelected0(event, indice, idTramiteRequisito, idRequisito) {
      console.log(event, indice, idTramiteRequisito, idRequisito);
      var extension=this.getExtensionFile(event.target.files[0].name);
      if(extension=='pdf'){
        var tamanio = (event.target.files[0].size / 1024 / 1024).toFixed(4);
        // console.log('tamanio======'+ tamanio);
        if (tamanio > 15) {
          this.$swal({
            icon: "info",
            title: "Aviso",
            text: "El archivo excede el tamaño permitido."
          });
        } else {
          this.listaRequisitoModelo[indice].nombreArchivo =
            event.target.files[0].name;
          this.listaRequisitoModelo[indice].archivo = event.target.files[0];
          if (this.idTramite == 0) {
            this.$swal({
              title: "Procesando",
              allowOutsideClick: false,
              onBeforeOpen: () => {
                this.$swal.showLoading();
              }
            });
            this.GuardarTramiteCompleto(0, 0, true, indice);
          } else {
            // if(idTramiteRequisito>0 || this.listaRequisitoModelo[indice].clase == 'btn btn-success'){
            if (idTramiteRequisito > 0) {
              // console.log('pintando indice'+indice);
              this.listaRequisitoModelo[indice].clase = "btn btn-danger";
              this.listaRequisitoModelo[indice].textoBotonCambiar = "Guardar";
              if (
                idTramiteRequisito > 0 &&
                this.listaRequisitoModelo[indice].clase == "btn btn-danger"
              ) {
                this.CambiarRequisitoTramite(
                  idTramiteRequisito,
                  idRequisito,
                  this.listaRequisitoModelo[indice].archivo,
                  this.listaRequisitoModelo[indice].nombreArchivo
                );
              }
            } else {
              this.$swal({
                title: "Procesando",
                allowOutsideClick: false,
                onBeforeOpen: () => {
                  this.$swal.showLoading();
                }
              });
              this.GuardarRequisitoTramite(
                idTramiteRequisito,
                idRequisito,
                event.target.files[0],
                event.target.files[0].name
              );
            }
          }
        } 
      }else{
        this.$swal({
            icon: "info",
            title: "Aviso",
            text: "Solo se permiten archivos pdf."
          });
      }
      
