    GuardarRequisitoTramite(idTramiteRequisito, idRequisito, archivo, nombre) {
      var RequisitoTramite = {};
      RequisitoTramite.idTramiteRequisito = idTramiteRequisito;
      var Tramite = {};
      Tramite.idTramite = this.idTramite;
      RequisitoTramite.tramite = {};
      RequisitoTramite.tramite = Tramite;
      var Requisito = {};
      Requisito.idRequisito = idRequisito;
      RequisitoTramite.requisito = {};
      RequisitoTramite.requisito = Requisito;
      RequisitoTramite.valor = nombre;
      var dataUsuarioPost = {};
      dataUsuarioPost.ideUsuario = localStorage.getItem(
        "idUsuarioLogueadoTramite"
      );
      //dataUsuarioPost.ideUsuario=343291;ario=343291;
      RequisitoTramite.usuarioCreador = dataUsuarioPost;
      if (this.estadoInicial == 27) {
        RequisitoTramite.observacion =
          idTramiteRequisito == 0 ? "Nuevo" : "Modificado";
      }
      // console.log('Requisito a enviar post');
      // console.log(RequisitoTramite);
      var dataPost = new FormData();
      dataPost.append("requisito", JSON.stringify(RequisitoTramite));
      dataPost.append("imgCroquis", archivo);
      this.$swal({
        title: "Procesando",
        allowOutsideClick: false,
        onBeforeOpen: () => {
          this.$swal.showLoading();
        }
      });
      var rutaprod = Constantes.rutaTramite;
      axios
        .post(rutaprod + "tramite-requisito-individual/", dataPost)
        .then(response => {
          if (response.data.success) {
            this.$swal.close();
            this.$swal({
              icon: "success",
              text: "El requisito fue registrado con éxito"
            });
            this.CargarTramiteRequisito();
          } else {
            this.$swal.close();
            this.$swal({
              icon: "info",
              title: "Aviso",
              text: "Sucedió un error. Favor vuelva a intentar en unos minutos."
            });
          }

          //console.log(this.listaTipoTramite[0].nombre);
        })
        .catch(e => console.log(e));
    },
