    ActivarTab(paso, orden) {
      var i = 0;
      var ordenOficial = 0;
      while (i < this.listaPasos.length && ordenOficial == 0) {
        // console.log('pintando pasos');
        // console.log(this.listaPasos);
        if (this.listaPasos[i].idPaso.idPasos == this.pasoOficial) {
          ordenOficial = this.listaPasos[i].orden;
        }
