    CambiarTab(nuevoPaso, orden) {
      // console.log('entró a cambiar paso final');
      this.pasoActual = nuevoPaso;
      this.ordenActual = orden;
      this.activeObservacion = "";
      this.activeDatosTramite = "";
      this.activeArchivo = "";
      this.activeFin = "";
      this.activePagos = "";
      this.lblContinuar = "Continuar";
      this.lblAtras = "Atrás";
      // if (this.pasoActual == -1) {
      //   this.activeObservacion = "active";
      // }
      if (this.mostrarObservaciones) {
        // if (this.pasoActual == -1) {
        //   this.activeObservacion = "active";
        //   this.lblAtras = "Salir";
        // }
        if (this.pasoActual == 1 || this.pasoActual == 2) {
          this.activeDatosTramite = "active";
          this.lblAtras = "Salir";
        }
      } else {
        if (this.pasoActual == 1 || this.pasoActual == 2) {
          this.activeDatosTramite = "active";
          this.lblAtras = "Salir";
        }
      }

      //if(this.pasoActual==3)this.activeFin='active';
      if (this.pasoActual == 3) {
        this.activePagos = "active";
        if (this.estadoInicial == 22 && this.mostrarVisa==false) {
          this.CargarVisa();
        }
      }
      if (this.pasoActual == 4) {
        this.activeFin = "active";
        this.lblContinuar = "Fin";
      }
      if (this.pasoActual == 5) this.activeArchivo = "active";
