    CambioSimple(nuevoPaso, orden, indicador) {
      var respuesta = true;
      if (indicador > -1) {
        this.$swal({
          title: "Procesando",
          allowOutsideClick: false,
          onBeforeOpen: () => {
            this.$swal.showLoading();
          }
        });
        var dataPost = new FormData();
        dataPost.append("idTramite", this.idTramite);
        dataPost.append("idTipoTramite", this.idTipoTramite);
        dataPost.append("idPasoActual", this.pasoActual);
        dataPost.append("idNuevoPaso", nuevoPaso);
        dataPost.append("idEstado", this.estadoInicial);
        dataPost.append("precio", this.precioTramite);
        dataPost.append("idUsuario", localStorage.getItem("idUsuarioLogueadoTramite"));
        console.log(dataPost)
        axios
          .post(Constantes.rutaTramite + "cambiarPaso/", dataPost)
          .then(response => {
            respuesta = response.data.success;
            if (respuesta) {
              this.$swal.close();
              this.pasoOficial = nuevoPaso;
              this.CambiarTab(nuevoPaso, orden);
            } else {
              this.permiteEditar = true;
              this.$swal.close();
              this.$swal({
                icon: "info",
                title: "Aviso",
                text:
                  "Sucedió un error. Favor vuelva a intentar en unos minutos."
              });
            }
          })
          .catch(e => console.log(e));
      } else {
        this.CambiarTab(nuevoPaso, orden);
      }
    },
    ActualizarTramite(nuevoPaso, orden) {
      // console.log('ingresó a actualizar trámite NUEVO PASO:' + nuevoPaso);
      var Tramite = this.LlenarObjetoTramite();
      var tramiteValidado = this.ValidarTramite(Tramite);
      var requisitosValidados = this.ValidarRequisitos();
      if (this.$route.params.idTramite > 0 && this.estadoInicial != 22) {
        Tramite.bloqueaActualizarEstado = true;
      } else {
        Tramite.bloqueaActualizarEstado = false;
        var Pasos = {};
        Pasos.idPasos = nuevoPaso;
        Tramite.pasoActual = {};
        Tramite.pasoActual = Pasos;
      }

      if (tramiteValidado && requisitosValidados) {
        var dataPost = new FormData();
        
        var rutaPost = Constantes.rutaTramite + "actualizar-tramite/";
        if (Tramite.idTramite == 0) {
          rutaPost = Constantes.rutaTramite + "tramite-requisito/";
          dataPost.append("imgCroquis", null);
        }else{
          Tramite.nombresSolicitante=null;
          dataPost.append("tramite", JSON.stringify(Tramite));
          rutaPost = Constantes.rutaTramite + "actualizar-tramite/";
        }
        var rutaprod = "http://localhost:8090/api/tramites/";
        axios
          .post(rutaPost, dataPost)
          //axios.post(rutadev,dataPost)
          .then(response => {
            this.idTramite = response.data.data;
            /* this.$swal({
                        icon: 'success',
                        text: 'El trámite fue registrado con éxito'
                      }); */
            this.CambioSimple(nuevoPaso, orden, -1);
            this.ObtenerListaAdjuntos();
            this.CargarTramite();
            this.lblAtras = "Atrás";
            //console.log(this.listaTipoTramite[0].nombre);
          })
          .catch(e => console.log(e));
      }
    },
    GuardarTramiteCompleto(nuevoPaso, orden, primerRequisito, indiceArchivo) {
      var Tramite = this.LlenarObjetoTramite();
      var Requisito = {};
      var tramiteValidado = true;
      var requisitosValidados = true;

      if (primerRequisito) {
        requisitosValidados = true;
      } else {
        this.tramiteValidado = this.ValidarTramite(Tramite);
        this.ValidarRequisitos();
      }
      if (tramiteValidado && requisitosValidados) {
        Requisito = this.LlenarObjetoRequisito(indiceArchivo);
        Tramite.listaRequisito = Requisito;
        var dataPost = new FormData();
        dataPost.append("tramite", JSON.stringify(Tramite));
        var i = 0;
        while (i < this.listaRequisitosArchivos.length) {
          dataPost.append("imgCroquis", this.listaRequisitosArchivos[i]);
          i++;
        }

        
        axios
          .post(Constantes.rutaTramite + "tramite-requisito/", dataPost)
          //axios.post(rutadev,dataPost)
          .then(response => {
            console.log("----------------------------");
            console.log(response);
            this.idTramite = response.data.data;
            if (!primerRequisito) {
              this.$swal({
                icon: "success",
                text: "El trámite fue registrado con éxito"
              });
              this.CambioSimple(nuevoPaso, orden, -1);
            } else {
              this.$swal({
                icon: "success",
                text: "El requisito se grabó correctamente"
              });
            }

            this.ObtenerListaAdjuntos();
            this.CargarTramite();
            //console.log(this.listaTipoTramite[0].nombre);
          })
          .catch(e => console.log(e));
      }
      // console.log('FIN METODO this.esAdministrado' + this.esAdministrado);
    },
    LlenarObjetoRequisito(indiceArchivo) {
      var i = 0;
      var indiceAuxiliar = 0;
      var ListaRequisitoTramite = [];
      this.listaRequisitosArchivos = [];
      while (i < this.listaRequisitoModelo.length) {
        if (i == indiceArchivo) {
          var requisito = this.listaRequisitoModelo[i];
          if (requisito.archivo != null) {
            var RequisitoTramite = {};
            RequisitoTramite.tipoTramite = {};
            var TipoTramite = {};
            TipoTramite.idTipoTramite = requisito.idTipoTramite;
            RequisitoTramite.tipoTramite = TipoTramite;
            RequisitoTramite.requisito = {};
            var Requisito = {};
            Requisito.idRequisito = requisito.idRequisito;
            RequisitoTramite.requisito = Requisito;
            RequisitoTramite.valor = requisito.nombreArchivo;
            RequisitoTramite.indiceAuxiliar = indiceAuxiliar;
            ListaRequisitoTramite.push(RequisitoTramite);
            indiceAuxiliar++;
            this.listaRequisitosArchivos.push(requisito.archivo);
          }
        }
        i++;
      }
      return ListaRequisitoTramite;
    },
    ValidarTramite(tramite) {
      var validado = true;
      if (tramite.flagAdministrado == "false") {
        if (tramite.numeroDocuentoSolicitante == "") {
          this.$swal({
            icon: "info",
            title: "Aviso",
            text: "Debe digitar su número de docmento"
          });
          validado = false;
          return validado;
        }
        if (tramite.nombresSolicitante == "") {
          this.$swal({
            icon: "info",
            title: "Aviso",
            text: "Debe digitar su nombre completo"
          });
          validado = false;
          return validado;
        }
        if (tramite.direccionSolicitante == "") {
          this.$swal({
            icon: "info",
            title: "Aviso",
            text: "Debe digitar su dirección"
          });
          validado = false;
          return validado;
        }
      }
      /* if (tramite.referencia == "") {
        this.$swal({
          icon: "error",
          title: "Error",
          text: "Debe digitar el asunto"
        });
        validado = false;
        return validado;
      } */
      return validado;
    },
    ValidarRequisitos() {
      var validado = true;
      var i = 0;
      while (i < this.listaRequisitoModelo.length && validado) {
        if (
          (this.listaRequisitoModelo[i].flagObligatorio &&
            !this.listaRequisitoModelo[i].urlDescarga) ||
          (!this.listaRequisitoModelo[i].idTramiteRequisito > 0 && this.listaRequisitoModelo[i].flagObligatorio)
        ) {
          this.$swal({
            icon: "info",
            title: "Aviso",
            text:
              "Debe registrar requisito: " + this.listaRequisitoModelo[i].nombre
          });
          validado = false;
        }
        i++;
      }
      return validado;
    },
