      var dataUsuarioPost = {};
      dataUsuarioPost.desLoginUser = localStorage.getItem("cuentaTramite");
      dataUsuarioPost.ideEstado = 1;
      dataUsuarioPost.idePersona = localStorage.getItem(
        "idPersonaLogueadoTramite"
      );
      dataUsuarioPost.ideRol = 6;
      dataUsuarioPost.ideUsuario = localStorage.getItem(
        "idUsuarioLogueadoTramite"
      );
      //dataUsuarioPost.ideUsuario=343291;
      dataUsuarioPost.inCredencialExpirada = "1";
      dataUsuarioPost.inCuentaBloqueada = "1";
      dataUsuarioPost.inCuentaExpirada = "1";
      dataUsuarioPost.inHabilitado = "1";
      var dataPersonaPost = {};
      dataPersonaPost.con = "N1360408";
      dataPersonaPost.dirEmail = "";
      dataPersonaPost.ideTipoDocuento = 31;
      dataPersonaPost.nomApellidoMaterno = localStorage.getItem(
        "maternoUsuarioLogueadoTramite"
      );
      dataPersonaPost.nomApellidopaterno = localStorage.getItem(
        "paternoUsuarioLogueadoTramite"
      );
      dataPersonaPost.nomNombres = localStorage.getItem(
        "nombreUsuarioLogueadoTramite"
      );
      dataPersonaPost.numNumeroDocuento = localStorage.getItem(
        "numeroDocumentoLogueadoTramite"
      );
      dataPersonaPost.perSoc = localStorage.getItem(
        "nombreCompletoLogueadoTramite"
      );
      dataUsuarioPost.persona = dataPersonaPost;
      dataExpedientePost.usuarioCreador = dataUsuarioPost;

      var dataPost = new FormData();
      //dataPost.append('expediente',JSON.stringify(dataExpedientePost));
      //dataPost.append('dataFile',this.adjuntoModelo);
      dataPost.append("expediente", JSON.stringify(dataExpedientePost));
      dataPost.append("imgCroquis", this.adjuntoModelo);
      
      axios
        .post(Constantes.rutaTramite + "tramite-web/", dataPost)
        //axios.post(rutadev,dataPost)
        .then(response => {
          // alert('Retorno');
          // console.log(response);
          //console.log(this.listaTipoTramite[0].nombre);
        })
        .catch(e => console.log(e));
    },
    validaNoDefinido() {},
    ObtenerUnidadesOrganicas() {
      axios
        .get(Constantes.rutaTramite + "unidadorganica/" + this.reemplazaVacio(""))
        .then(response => {
          this.listaUnidadesOrganicas = response.data.data;
          var i = 0;
          while (i < this.listaUnidadesOrganicas.length) {
            var unidad = {};
            unidad.value = this.listaUnidadesOrganicas[i].uniorgcod;
            unidad.text = this.listaUnidadesOrganicas[i].uniorgnom;
            this.mapListUnidadesOrganicas.push(unidad);
            i++;
          }
        })
        .catch(e => console.log(e));
    }
  },
  computed: {
    selectOptions() {
      return this.listaUnidadesOrganicas.map(g => ({
        label: g.uniorgnom,
        value: g.uniorgnom
      }));
    },
    validaNoDefinidoCheck() {
      return this.marcado;
    },
    fechaActualx() {
      let fc = this.fecTramite;
      if (fc == null) {
        fc = new Date();
      }
      //console.log(fc.getUTCFullYear());
      return (
        fc.getUTCDate() +
        "/" +
        (fc.getUTCMonth() + 1) +
        "/" +
        fc.getUTCFullYear()
      );
    }
  }
};
</script>
